@import url(https://fonts.googleapis.com/css?family=Lato:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.color-picker {
  margin: 0 10px; }
  .color-picker__input {
    width: 36px;
    height: 36px;
    border-radius: 4px;
    background-color: #ff3b49;
    cursor: pointer;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.25);
    transition: box-shadow 0.5s; }
  .color-picker__input:hover {
    box-shadow: 1px 1px 20px rgba(0, 0, 0, 0.25); }
  .color-picker .picker-wrapper {
    position: absolute; }
  .color-picker .picker-handler {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }

.header {
  width: 100%;
  height: 64px;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 2;
  justify-content: center; }
  .header .container {
    display: flex;
    align-items: center; }
  .header .midpoint-field {
    padding: 0;
    margin: 0 10px;
    width: 36px;
    height: 36px;
    border: 1px solid rgba(51, 51, 51, 0.45098);
    text-align: right;
    border-radius: 4px; }

@media screen and (max-width: 768px) {
  .header {
    position: fixed;
    background-color: white; }
    .header .midpoint-field {
      text-align: center; } }

.color {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  flex: 1 1;
  color: #000;
  padding: 20px;
  min-width: calc(100vw / 10);
  cursor: pointer;
  position: relative; }
  .color--dark {
    color: #fff; }
  .color__copy {
    opacity: 0;
    position: absolute;
    top: 50%;
    padding: 7px 2rem;
    height: auto;
    border: 2px solid rgba(255, 255, 255, 0.2);
    border-radius: 4px;
    line-height: 2rem;
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: bold;
    color: #fff;
    transition: transform 0.2s, opacity 0.3s, -webkit-transform 0.2s; }
    .color:hover .color__copy {
      opacity: 1; }
  .color__code {
    font-size: 1.2rem;
    line-height: 1.5rem; }

@media screen and (max-width: 768px) {
  .color {
    min-width: auto;
    min-height: calc(100vh / 8); }
    .color:hover .color__copy {
      opacity: 0; } }

.colors-wrapper {
  display: flex;
  height: calc(100vh - 64px);
  overflow-y: hidden; }

.colors {
  display: flex;
  flex-direction: row;
  flex: 1 1; }

@media screen and (max-width: 768px) {
  .colors-wrapper {
    height: auto;
    min-height: 100vh;
    width: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    padding-top: 64px; }
  .colors {
    flex-direction: column; } }

.copied {
  position: fixed;
  opacity: 0;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #4b6d89;
  align-items: center;
  justify-content: center;
  transition: opacity 0.4s;
  pointer-events: none;
  z-index: 3; }
  .copied.active {
    opacity: 1;
    display: flex; }
  .copied__text-wrapper {
    padding: 5rem;
    background: #c4c0c04d;
    width: 100%;
    text-align: center;
    font-size: 5rem; }

.App {
  display: flex;
  flex-direction: column;
  height: 100%; }

